import React, { useState,useEffect } from "react";
import cn from "classnames";
import { Link, useHistory } from "react-router-dom";
import styles from "./Admin.module.sass";
import Icon from "../../components/Icon";
import Form from "../../components/Form";
import Control from "../../components/Control";
import TextInput from "../../components/TextInput";
import TextArea from "../../components/TextArea";
import swal from 'sweetalert';
import { ethers, utils } from "ethers";
import { useWeb3React } from "@web3-react/core";

const contract = require("../../artifacts/101010art.json");
const contractAddress = "0x84FD15bfd538f3EA8c48d7433243CC82Df6E03e8";
const contractAbi = contract.abi;
const chainID = 1;

const Admin = () => {
  const [search, setSearch] = useState("");
  const [contract, setContract] = useState("");
  const [reserveaddress, setReserveAddress] = useState("");
  const [reservecounts, setReserveCounts] = useState("");
  const [whiltelistaddress, setWhitelistAddress] = useState("");
  const [freemintwhiltelistaddress, setFreemintWhitelistAddress] = useState("");
  const [mintedCnts, setMintedCnts] = useState(0);
  const [totalCnts, setTotalCnts] = useState(0);
  const [rmintedCnts, setRMintedCnts] = useState(0);
  const [rtotalCnts, setRTotalCnts] = useState(0);
  const [pubstatus, setPubStatus] = useState("");
  const [checkstatus, setChecktatus] = useState("");
  const { account, connector, activate, library, chainId, active } = useWeb3React();

  const handleSubmit = (e) => {
    alert();
    swal({ title: "Unauthorized Access", icon: "error" }) 
  };

  const connectContract = (e) => {
    
    swal({ title: "Unauthorized Access", icon: "error" }) 
  };

  const mintReserve = async() => {
    if (!account) {
      swal({ title: "Please connect the wallet", icon: "error" })
      return
    }
    try {
      let tx = await contract.reserve(reserveaddress,reservecounts);
      swal({ title: "Successfuly minted", icon: "success" }) 
    } catch (e) {
      swal({ title: e, icon: "error" })
    }
  }

  const addWhitelist = async() => {
    if (!account) {
      swal({ title: "Please connect the wallet", icon: "error" })
      return
    }
    try {
      let arrAddress = whiltelistaddress.split(",")
      let tx = await contract.addWhitelisted(arrAddress);
      swal({ title: "Successfuly added", icon: "success" }) 
    } catch (e) {
      swal({ title: e, icon: "error" })
    }
  }

  const removeWhitelist = async() => {
    if (!account) {
      swal({ title: "Please connect the wallet", icon: "error" })
      return
    }
    try {
      let arrAddress = whiltelistaddress.split(",")
      let tx = await contract.removeWhitelisted(arrAddress);
      swal({ title: "Successfuly removed", icon: "success" }) 
    } catch (e) {
      swal({ title: e, icon: "error" })
    }
  }

  const addFreemintWhitelist = async() => {
    if (!account) {
      swal({ title: "Please connect the wallet", icon: "error" })
      return
    }
    try {
      let arrAddress = whiltelistaddress.split(",")
      let tx = await contract.addFreemintWhitelisted(arrAddress);
      swal({ title: "Successfuly added", icon: "success" }) 
    } catch (e) {
      swal({ title: e, icon: "error" })
    }
  }

  const removeFreemintWhitelist = async() => {
    if (!account) {
      swal({ title: "Please connect the wallet", icon: "error" })
      return
    }
    try {
      let arrAddress = whiltelistaddress.split(",")
      let tx = await contract.removeFreemintWhitelisted(arrAddress);
      swal({ title: "Successfuly added", icon: "success" }) 
    } catch (e) {
      swal({ title: e, icon: "error" })
    }
  }

  const stopPublicSale = async() => {
    if (!account) {
      swal({ title: "Please connect the wallet", icon: "error" })
      return
    }
    try {
      let tx = await contract.pause(true);
      setPubStatus("stopped")
      swal({ title: "Successfuly stopped", icon: "success" }) 
    } catch (e) {
      swal({ title: e, icon: "error" })
    }
  }

  const startPublicSale = async() => {
    if (!account) {
      swal({ title: "Please connect the wallet", icon: "error" })
      return
    }
    try {
      let tx = await contract.pause(false);
      setPubStatus("started")
      swal({ title: "Successfuly started", icon: "success" }) 
    } catch (e) {
      swal({ title: e, icon: "error" })
    }
  }

  const checkWhitelist = async() => {
    if (!account) {
      swal({ title: "Please connect the wallet", icon: "error" })
      return
    }
    try{
      let tx_result = await contract.isWhitelisted(whiltelistaddress);
      let dis_title = '';
      if(tx_result) dis_title = 'Listed on whiltelist.'
      else dis_title = 'Not listed on whiltelist'
      swal({ title: dis_title, icon: "info" })
    }
    catch(e){
      swal({ title: e, icon: "error" })
    }
  }

  useEffect(async () => {
    try {
      //console.log(active)
      if (chainId != chainID) {
        if (chainId) swal({ title: "Wrong Network (Please connect with Ethereum)", icon: "error" }) 
        else swal({ title: "Please connect the wallet.", icon: "info" })
        //history.push("/");
        return;
      }
      let provider;
      
      provider = library

      // window.ethereum
      //   .enable()
      //   .then((provider = new ethers.providers.Web3Provider(window.ethereum)));
      const signer = provider.getSigner(account);
      
      let nftContract = new ethers.Contract(
        contractAddress,
        contractAbi,
        signer
      );
      if (nftContract) {
        console.log('effect',signer)
        setContract(nftContract);
        let _totalCnts = await nftContract.MAX_SUPPLY();
        let _mintedCnts = await nftContract.totalSupply();
        let _rtotalCnts = 10//await nftContract.MAX_SUPPLY();
        let _rmintedCnts = await nftContract._reserved();
        let _pubstatus = await nftContract.isPaused();
        setMintedCnts(_mintedCnts);
        setTotalCnts(_totalCnts);
        setRMintedCnts(_rmintedCnts);
        setRTotalCnts(_rtotalCnts);
        if(_pubstatus==false) setPubStatus("started");
        else setPubStatus("stopped");

      }
    } catch (e) {
      swal({ title: e, icon: "error" }) 
    }
  }, [account]);

  return (
    <div className={styles.page}>
      <div className={cn("section-pt80", styles.section)}>
        <div className={cn("container", styles.container)}>
              <div className={styles.list}>
                <div className={styles.item}>
                <div className={styles.mintCnts}>
                  {"Total: "+ mintedCnts + " of " + totalCnts + " unique artworks minted"}<br/>
                  {"Reserve: "+ (rtotalCnts - rmintedCnts) + " of " + rtotalCnts + " unique artworks minted"}
                </div>
                  <div className={styles.category}>Reserved Sale</div>
                  <div className={styles.fieldset}>
                    <TextInput
                      className={styles.field}
                      label="Wallet address"
                      name="Wallet"
                      type="text"
                      value={reserveaddress}
                      onChange={(e) => setReserveAddress(e.target.value)}
                      placeholder="Enter the wallet address for sale"
                      required
                    />
                    <TextInput
                      className={styles.field}
                      label="Amount"
                      name="Amount"
                      type="number"
                      value={reservecounts}
                      onChange={(e) => setReserveCounts(e.target.value)}
                      placeholder="Enter the amount of NFT"
                      required
                    />
                  </div>
                </div>
              </div>
              <div className={styles.btns}>
                <button className={cn("button", styles.button)} onClick={mintReserve}>
                  Mint
                </button>
              </div>
        </div>
        <div className={cn("container", styles.container)}>
              <div className={styles.list}>
                <div className={styles.item}>
                  <div className={styles.category}>Private Sale</div>
                  You can input the multi address to add or remove on whitelist.
                  <br />
                  (ex. 0x6Be0...c91b,0x7Ce1...c91b,0x923A...D303)<br />
                  But you have to input the single address to check if the address is listed on whitelist.
                  <br /><br />
                  <div className={styles.fieldset}>
                    <TextInput
                      className={styles.field}
                      label="Wallet address"
                      name="Wallet"
                      type="text"
                      value={whiltelistaddress}
                      onChange={(e) => setWhitelistAddress(e.target.value)}
                      placeholder="Enter the wallet address for whitelist"
                      required
                    />
                    
                  </div>
                </div>
              </div>
              <div className={styles.btns}>
                <button className={cn("button", styles.button)} onClick={checkWhitelist}>
                  Check
                </button>
                <button className={cn("button", styles.button)} onClick={addWhitelist}>
                  Add
                </button>
                <button className={cn("button", styles.button)} onClick={removeWhitelist}>
                  Remove
                </button>
              </div>
        </div>
        <div className={cn("container", styles.container)}>
              <div className={styles.list}>
                <div className={styles.item}>
                  <div className={styles.category}>Giveways Sale</div>
                  <div className={styles.fieldset}>
                    <TextInput
                      className={styles.field}
                      label="Wallet address"
                      name="Wallet"
                      type="text"
                      value={freemintwhiltelistaddress}
                      onChange={(e) => setFreemintWhitelistAddress(e.target.value)}
                      placeholder="Enter the wallet address for giveways"
                      required
                    />
                    
                  </div>
                </div>
              </div>
              <div className={styles.btns}>
                <button className={cn("button", styles.button)} onClick={addFreemintWhitelist}>
                  Add
                </button>
                <button className={cn("button", styles.button)} onClick={removeFreemintWhitelist}>
                  Remove
                </button>
              </div>
        </div>
        <div className={cn("container", styles.container)}>
              <div className={styles.list}>
                <div className={styles.item}>
                  <div className={styles.category}>Public Sale</div>
                  <div className={styles.fieldset}>
                  {"Status: "+ pubstatus}
                  </div>
                </div>
              </div>
              <div className={styles.btns}>
                <button className={cn("button", styles.button)} onClick={startPublicSale}>
                  Start
                </button>
                <button className={cn("button", styles.button)} onClick={stopPublicSale}>
                  Stop
                </button>
              </div>
        </div>
      </div>
    </div>
  );

};

export default Admin;
